import React from 'react'

import './TableHeader.css'

import { useAppSelector } from 'hooks/redux'
import { selectActiveTableCurrentSequenceCell } from 'store/selectors/activeTable'
import { TableCellBackground } from '../TableCellBackground/TableCellBackground'
import { TableCellSymbol } from '../TableCellSymbol/TableCellSymbol'

import { ICell, TypeSymbolColorMode } from 'types/table'
import { classNames } from '@vkontakte/vkui'

export const TableHeader: React.FC = () => {
   return (
      <div className={classNames('TableHeader')}>
         <TableFindCell />
      </div>
   )
}

export const TableFindCell: React.FC = () => {
   const currentSequenceCell = useAppSelector(selectActiveTableCurrentSequenceCell)

   const isBlack = currentSequenceCell.sequenceValue.startsWith('+')
   const isRed = currentSequenceCell.sequenceValue.startsWith('-')

   let symbolColorMode: TypeSymbolColorMode = currentSequenceCell.symbolColorMode

   if (isBlack || isRed) {
      symbolColorMode = 'white'
   }
   if (symbolColorMode === 'custom') {
      symbolColorMode = 'primary'
   }

   const cellProps: ICell = {
      ...currentSequenceCell,
      symbolColorMode,

      visibility: 'visible',
   }
   return (
      <div style={{ width: 48, height: 48 }}>
         <TableCellBackground backgroundColorMode={cellProps.backgroundColorMode} rounded>
            <TableCellSymbol symbolValue={cellProps.symbolValue} symbolColorMode={cellProps.symbolColorMode} />
         </TableCellBackground>
      </div>
   )
}
