import React from 'react'
import { FixedLayout, PanelHeader, PanelHeaderProps, Separator, useAdaptivityWithJSMediaQueries } from '@vkontakte/vkui'

export const PanelHeaderSeparator: React.FC<PanelHeaderProps> = (panelHeaderProps) => {
   const { isDesktop } = useAdaptivityWithJSMediaQueries()

   return (
      <>
         <PanelHeader separator={isDesktop} {...panelHeaderProps}></PanelHeader>
         {!isDesktop && (
            <FixedLayout vertical='top'>
               <Separator wide />
            </FixedLayout>
         )}
      </>
   )
}
