import React from 'react'
import { GroupCard } from 'components/ui/GroupCard/GroupCard'
import { CellTableModeSearch } from './cells/CellTableModeSearch'
import { Separator } from '@vkontakte/vkui'
import { CellTableShowTime } from './cells/CellTableShowTime'
import { CellTableSelected } from './cells/CellTableSelected'

export const GroupTableSettings: React.FC = () => {
   return (
      <GroupCard separator='hide'>
         <CellTableModeSearch />
         <Separator wide />
         <CellTableSelected />
         <Separator wide />
         <CellTableShowTime />
      </GroupCard>
   )
}
