import React from 'react'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { useAppSelector } from 'hooks/redux'
import { selectChangeableTableSymbolsSearchMode } from 'store/selectors/tableGenerateOptions'
import { TABLE_SYMBOLS_SEARCH_MODE } from 'data/table'
//TODO delete "SYMBOLS"
export const IndicatorTableModeSearch: React.FC = () => {
   const symbolsSearchMode = useAppSelector(selectChangeableTableSymbolsSearchMode)
   return <IndicatorTitle>{TABLE_SYMBOLS_SEARCH_MODE[symbolsSearchMode]}</IndicatorTitle>
}
