import React from 'react'
import { ButtonGroup, Group, Panel, PanelHeaderButton, PanelProps, Tappable } from '@vkontakte/vkui'

import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { Icon28SettingsOutline, Icon48Play, Icon56PlayGesture } from '@vkontakte/icons'
import { useRouter } from '@happysanta/router'
import { PAGE_OPTIONS } from 'router/pages'

import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'
import { useAppSelector } from 'hooks/redux'

import { TableView } from 'components/ui/TableView/TableView'

import { TableWrapper } from 'components/ui/TableWrapper/TableWrapper'
import { ButtonTableOptions, ButtonTableStart } from 'components/ui/TableButtons/TableButtons'

export const PanelMain: React.FC<PanelProps> = (panelProps) => {
   const router = useRouter()

   const tableSize = useAppSelector((state) => state.tableGenerateOptions.changeableTableOptions.size)
   const displayedCells = useAppSelector((state) => state.tableGenerateOptions.table.displayedCells)

   const onClickSettings = () => {
      router.pushPage(PAGE_OPTIONS)
   }

   return (
      <Panel {...panelProps}>
         <PanelHeaderSeparator
            // visor={false}
            before={
               <PanelHeaderButton aria-label='a' onClick={onClickSettings}>
                  <Icon28SettingsOutline />
               </PanelHeaderButton>
            }
         >
            Таблицы Шульте
         </PanelHeaderSeparator>
         <Group className='Group--stretched Group--align-center'>
            <TableWrapper
               style={{ paddingTop: 48 }}
               table={
                  <TableView
                     disabled
                     tableSize={tableSize}
                     displayedCells={displayedCells}
                     children={
                        <Tappable
                           activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                           hasActive
                           hasHover
                           activeMode='background'
                           style={{
                              position: 'absolute',
                              width: '100%',
                              height: '100%',
                              display: 'flex',
                              justifyContent: 'center',
                              alignItems: 'center',
                              borderRadius: 8,
                              backgroundColor: 'rgba(0,0,0,.1)',
                           }}
                        >
                           <div
                              style={{
                                 width: 96,
                                 height: 96,
                                 backgroundColor: 'gray',
                                 borderRadius: '50%',
                                 display: 'flex',
                                 justifyContent: 'center',
                                 alignItems: 'center',
                              }}
                           >
                              <Icon48Play width={56} height={56} />
                           </div>
                        </Tappable>
                     }
                  />
               }
               bottom={
                  <ButtonGroup style={{ alignSelf: 'center' }} stretched align='center' mode='vertical'>
                     <ButtonTableStart />
                     <ButtonTableOptions />
                  </ButtonGroup>
               }
            />

            {/*<div style={{ position: 'absolute', width: '100%', height: '100%', top: 0, left: 0 }} />*/}
         </Group>
      </Panel>
   )
}

