import React from 'react'
import { ActionSheet, ActionSheetItem } from '@vkontakte/vkui'
import { NavProp } from 'types/nav'
import { useRouter } from '@happysanta/router'
import { useAnchorElement } from '../PopoutContext'
import { ActionSheetCloseItem } from './ActionSheetCloseItem'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { selectChangeableTableDirectionSequence } from 'store/selectors/tableGenerateOptions'
import { setTableOptionsDirectionSequence } from 'store/slices/tableGenerateOptions'

import { Icon24DoneOutline } from '@vkontakte/icons'
import { dataTableDirectionsSequence, TABLE_DIRECTION_SEQUENCE } from 'data/table'
import { TypeTableSequence } from 'types/table'

export const ActionSheetTableOptionsSequence: React.FC<NavProp> = () => {
   const router = useRouter()
   const dispatch = useAppDispatch()
   const anchorElement = useAnchorElement()

   const changeableDirectionSequence = useAppSelector(selectChangeableTableDirectionSequence)

   const onClose = () => router.popPageIfPopup()

   const onChangeDirectionSequence: React.ChangeEventHandler<HTMLInputElement> = (e) => {
      dispatch(setTableOptionsDirectionSequence(e.target.value as TypeTableSequence))
   }

   return (
      <ActionSheet toggleRef={anchorElement} onClose={onClose} iosCloseItem={<ActionSheetCloseItem />}>
         {dataTableDirectionsSequence.map((directionSequence) => (
            <ActionSheetItem
               selectable
               onChange={onChangeDirectionSequence}
               checked={changeableDirectionSequence === directionSequence}
               iconChecked={<Icon24DoneOutline />}
               value={directionSequence}
               autoClose
               key={directionSequence}
            >
               {TABLE_DIRECTION_SEQUENCE[directionSequence]}
            </ActionSheetItem>
         ))}
      </ActionSheet>
   )
}
