import React from 'react'
import { SimpleCell } from '@vkontakte/vkui'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { PAGE_SETTINGS_MODE_SEARCH } from 'router/pages'
import { useRouter } from '@happysanta/router'
import { IndicatorTableModeSearch } from '../indicators/IndicatorTableModeSearch'

export const CellTableModeSearch: React.FC = () => {
   const router = useRouter()

   const onClickSettingsModeSearch = () => {
      router.pushPage(PAGE_SETTINGS_MODE_SEARCH)
   }
   return (
      <SimpleCell
         activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
         onClick={onClickSettingsModeSearch}
         expandable
         indicator={<IndicatorTableModeSearch />}
      >
         Режим поиска
      </SimpleCell>
   )
}
