import React from 'react'
import { ActionSheet, ActionSheetItem } from '@vkontakte/vkui'
import { NavProp } from 'types/nav'
import { useRouter } from '@happysanta/router'
import { useAnchorElement, useSetAnchorElement } from '../PopoutContext'
import { ActionSheetCloseItem } from './ActionSheetCloseItem'
import { dataSymbolsSearchModes, TABLE_SYMBOLS_SEARCH_MODE } from 'data/table'
import { useAppDispatch, useAppSelector } from 'hooks/redux'
import { selectChangeableTableSymbolsSearchMode } from 'store/selectors/tableGenerateOptions'
import { Icon24DoneOutline } from '@vkontakte/icons'
import { setTableOptionsSymbolsSearchMode } from 'store/slices/tableGenerateOptions'
import { TypeTableSymbolsSearchMode } from 'types/table'

export const ActionSheetTableOptionsSymbolsSearchMode: React.FC<NavProp> = () => {
   const anchorElement = useAnchorElement()
   const setAnchorElement = useSetAnchorElement()
   const dispatch = useAppDispatch()
   const router = useRouter()
   const symbolsSearchMode = useAppSelector(selectChangeableTableSymbolsSearchMode)

   const onClose = () => {
      router.popPageIfPopup()
      setAnchorElement(null)
   }

   const onChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
      dispatch(setTableOptionsSymbolsSearchMode(e.target.value as TypeTableSymbolsSearchMode))
   }
   return (
      <ActionSheet onClose={onClose} toggleRef={anchorElement} iosCloseItem={<ActionSheetCloseItem />}>
         {dataSymbolsSearchModes.map((value) => (
            <ActionSheetItem
               selectable
               onChange={onChange}
               checked={symbolsSearchMode === value}
               iconChecked={<Icon24DoneOutline />}
               value={value}
               autoClose
               key={value}
            >
               {TABLE_SYMBOLS_SEARCH_MODE[value]}
            </ActionSheetItem>
         ))}
      </ActionSheet>
   )
}
