import React from 'react'
import { IndicatorTitle } from 'components/ui/IndicatorTitle/IndicatorTitle'
import { useAppSelector } from 'hooks/redux'
import { selectChangeableTableStyleSelected } from 'store/selectors/tableGenerateOptions'
import { TABLE_STYLE_SELECTED } from 'data/table'

export const IndicatorTableSelected: React.FC = () => {
   const styleSelected = useAppSelector(selectChangeableTableStyleSelected)
   return <IndicatorTitle>{TABLE_STYLE_SELECTED[styleSelected]}</IndicatorTitle>
}
