import React from 'react'
import { useRouter } from '@happysanta/router'
import { Icon12ArrowUpRight } from '@vkontakte/icons'
import {
   Avatar,
   Card,
   CardGrid,
   FixedLayout,
   FormItem,
   Group,
   Header,
   Image,
   Panel,
   PanelHeader,
   PanelProps,
   RichCell,
   SegmentedControl,
   Separator,
   SimpleCell,
} from '@vkontakte/vkui'

import { Icon24DoneOutline, Icon24CancelOutline, Icon24SearchOutline } from '@vkontakte/icons'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'
import { TableCellSymbol } from 'components/ui/TableCellSymbol/TableCellSymbol'
import { TableCellBackground } from 'components/ui/TableCellBackground/TableCellBackground'
import { getRandomColor } from 'utils/color'

export const PanelProfile: React.FC<PanelProps> = (panelProps) => {
   // eslint-disable-next-line @typescript-eslint/no-unused-vars
   const router = useRouter()

   return (
      <Panel {...panelProps}>
         <PanelHeader separator={false}>Профиль</PanelHeader>
         <FixedLayout vertical='top' filled>
            <Separator wide />
         </FixedLayout>

         <Group>
            {/*   <Group mode='plain'>
               <CardGrid size='l'>
                  <Card>
                     <RichCell before={<Avatar size={48} />} activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}>
                        user
                     </RichCell>
                  </Card>
               </CardGrid>
            </Group> */}

            {/*   <StatsClicks /> */}

            {/* <Group header={<Header mode='secondary'></Header>}></Group> */}
            <Group mode='plain'>
               <SimpleCell expandable indicator={'158'}>
                  Решённые таблицы
               </SimpleCell>
            </Group>

            <Group mode='plain' header={<Header mode='secondary'>Найдено символов</Header>}>
               <SimpleCell indicator={'4К'}>Всего</SimpleCell>
            </Group>
            <Group mode='plain' header={<Header mode='secondary'>Нажатия</Header>}>
               <SimpleCell indicator={'3К'}>Всего</SimpleCell>
               <SimpleCell indicator={'2.8К'}>Правильные</SimpleCell>
               <SimpleCell indicator={'212'}>Неправильные</SimpleCell>
               <SimpleCell indicator={percentFormat.format(2800 / 3000)}>Точность</SimpleCell>
            </Group>
            <Group mode='plain' header={<Header mode='secondary'>Время</Header>}>
               <SimpleCell indicator={'3с'}>Лучшее время</SimpleCell>
               <SimpleCell indicator={'14с'}>Среднее время</SimpleCell>
               <SimpleCell indicator={'0.6c'}>Время поиска</SimpleCell>
            </Group>
         </Group>
      </Panel>
   )
}
const percentFormat = new Intl.NumberFormat(undefined, {
   style: 'percent',
   minimumFractionDigits: 2,
   maximumFractionDigits: 2,
})
const StatsClicks: React.FC = () => {
   return (
      <Group mode='plain'>
         <SimpleCell
            before={
               <Image size={48}>
                  <TableCellBackground backgroundColorMode='custom' backgroundColor={getRandomColor()}>
                     <TableCellSymbol symbolValue='24' symbolColorMode='white' />
                  </TableCellBackground>
               </Image>
            }
            after={<Icon24DoneOutline width={28} height={28} fill='green' />}
         >
            1.78с
         </SimpleCell>
      </Group>
   )
}
