import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { AppearanceType, ParentConfigData } from '@vkontakte/vk-bridge'
import { VKWebAppSetViewSettings } from 'API/bridge'
import { ConfigAppearanceType, dataConfigAppearances } from 'data/config'
import { setAppearance } from 'store/asyncThunks/config'
import { PromoBannerData } from 'types/vkui'

interface ISettingsNotifications {}

export interface IConfigAppearance {
   type: ConfigAppearanceType
   value: AppearanceType
}

interface IConfigState {
   isOnline: boolean
   sound: boolean
   vibration: boolean

   appearance: IConfigAppearance

   notifications: ISettingsNotifications

   clientConfig: ParentConfigData

   promoBannerData: PromoBannerData
}

const initialState: IConfigState = {
   isOnline: navigator.onLine,
   sound: false,
   vibration: true,
   appearance: {
      type: 'auto',
      value: 'light',
   },
   notifications: {},

   clientConfig: {} as ParentConfigData,
   promoBannerData: {},
}

const configSlice = createSlice({
   name: 'config',
   initialState,
   reducers: {
      //config

      setOnline: (state, action: PayloadAction<boolean>) => {
         state.isOnline = action.payload
      },
      setSound: (state, action: PayloadAction<boolean>) => {
         state.sound = action.payload
      },
      setVibration: (state, action: PayloadAction<boolean>) => {
         state.vibration = action.payload
      },

      setClientConfig: (state, action: PayloadAction<ParentConfigData>) => {
         state.clientConfig = action.payload

         if (state.appearance.type === 'auto') {
            state.appearance.value = state.clientConfig.appearance
            VKWebAppSetViewSettings(state.clientConfig.appearance)
         }
      },
   },
   extraReducers: (builder) => {
      builder.addCase(setAppearance.pending, (state, action) => {})
      builder.addCase(setAppearance.fulfilled, (state, action) => {
         state.appearance.type = action.payload.type
         state.appearance.value = action.payload.value
      })
      builder.addCase(setAppearance.rejected, (state, action) => {})
   },
})

export const { setOnline, setSound, setVibration, setClientConfig } = configSlice.actions

export const configReducer = configSlice.reducer
